<template>
  <skeleton v-if="skeletonLoading" :nums="4"></skeleton>
  <div v-else class="list-container" id="list">
    <div class="header">
      <div class="header-left">可用海贝 {{totalPoints}}</div>
      <div class="header-right"><span v-if="expireData && expirePoints">{{expireData.replace(/-/g,'/')|formatTime}}过期海贝 {{expirePoints}}</span><span class="info-icon" @click="toInfo"></span></div>
    </div>
    <div class="content">
      <van-pull-refresh v-model="isLoading" :disabled="list.length<6" @refresh="onRefresh">
        <template #pulling><refreshLoading text="下拉刷新"/></template>
        <template #loosing><refreshLoading text="下拉刷新"/></template>
        <template #loading><refreshLoading text="加载中"/></template>
        <template #success><refreshLoading text="加载完成"/></template>
        <van-list
          v-model="loading"
          :finished="finished"
          :finishedText="''"
          @load="onLoad"
          offset="100"
          van-clearfix
          :immediate-check="false"
        >
          <div class="item" v-for="(item, index) in list" :key="index" @click="toDetail(item)">
            <div class="item-left">
              <div class="left-icon"></div>
              <div class="left-info">
                <div class="point-info">{{item.description}}</div>
                <div class="point-type">{{transType(item.type)}}</div>
                <div class="point-time">{{item.createTime.replace(/-/g,'/')|formatTime}}</div>
                <!-- <div class="point-time">{{new Date(item.createTime.replace(/-/g,'/'))}}</div> -->

              </div>
            </div>
            <div class="item-right">{{item.amount}}</div>
          </div>
        </van-list>
        <!-- <div v-if="finished && productTrackList && productTrackList.cards.length === 0" class="no-data">
          <div class="nodata-icon"> </div>
          <div class="no-bottom">当前订单暂无物流信息</div>
        </div> -->
      </van-pull-refresh>

    </div>
    <div class="bottom">
      <div class="btn" @click="toMarket">兑换福利</div>
    </div>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import refreshLoading from '@/components/refreshLoading'

import {
  findIntegral,
  findDueSoonIntegral,
  findIntegralDetail,
} from '@/http/service/points'

export default {
  components: {
    skeleton,
    refreshLoading
  },
  data() {
    return {
      skeletonLoading: true,
      list: [], // 积分明细列表
      expireData: null, // 积分即将过期时间
      expirePoints: 0, // 即将过期的积分
      totalPoints: 0, // 总积分
      param: {
        pageNo: 1,
        size: 10,
        // memberId: 100

      },
      loading: false,
      finished: false,
      isLoading: false,
    }
  },
  created() {
    this.initData()
  },
  mounted() {
  },
  watch: {

  },
  methods: {
    /**
     * @description 初始化积分数据
    */
    initData() {
      // 1. 初始化积分列表
      this.getPointList()
      // 2. 初始化总积分
      this.getPointTotal()
      // 3. 初始化即将过期积分
      this.getExpirePoint()
    },
    /**
     * @description 获取积分列表
    */
    getPointList() {
      // this.loading = true
      findIntegralDetail(this.param).then(res => {
        console.log('findIntegralDetail', res)
        if (res.success) {
          if (this.list.length === 0 || this.param.pageNo === 1) {
            // 首次加载
            this.list = res.data.list
          } else {
            this.list = this.list.concat(res.data.list)
          }
          // 加载状态结束
          this.finished = false;
          this.loading = false;
          // 下啦刷新完成
          if (this.isLoading) {
            // this.$toast.success('刷新成功');
            this.isLoading = false;
          }
          if (this.list.length >= res.data.total) {
            this.finished = true
          }
          this.skeletonLoading = false
        }
      })
    },
    /**
     * @description 获取积分总数
    */
    getPointTotal() {
      findIntegral({}).then(res => {
        if (res.success) {
          this.totalPoints = res.data.integralValue
        }
      })
    },
    /**
     * @description 查询即将过期的积分
    */
    getExpirePoint() {
      // memberId: 100
      findDueSoonIntegral({}).then(res => {
        if (res.success) {
          this.expireData = res.data.expirationTime
          this.expirePoints = res.data.dueSoonIntegraCount
        }
      })
    },
    /**
     * @description 下拉刷新
    */
    onRefresh() {
      this.param.pageNo = 1
      // this.finished = false
      this.loading = false;

      // this.isLoading = true;

      // this.list = []
      this.getPointList()
    },
    /**
     * @description 上拉刷新
    */
    onLoad() {
      if (this.finished) return
      this.param.pageNo += 1
      this.getPointList()
    },
    transType(type) {
      let ret = ''
      switch (type) {
        case 1:
          ret = '新增'
          break;
        case 2:
          ret = '减少'
          break;
        case 3:
          ret = '冻结'
          break;
      }
      return ret
    },
    /**
     * @description 跳转积分商城
    */
    toMarket() {
      this.$util.openNewWindow(`https://jf.haier.com/m/#/index2?access_token=${this.$store.state.accessToken}`, true, false, false)
    },
    /**
     * @description 跳转积分详情
    */
    toDetail(item) {
      this.$router.push({ path: '/point-detail', query: { ruleName: item.ruleName, amount: item.amount, type: this.transType(item.type), description: item.description, createTime: item.createTime } })
    },
    /**
     * @description 跳转积分介绍
    */
    toInfo() {
      this.$router.push({ path: '/point-info' });
    }
  },
}
</script>

