/**
 * 积分相关
 */
import URL from '../url.js'
import { get, post } from '../http'
// const HOST = require('@/config')

// 查询可用总积分
export const findIntegral = data => get(URL.findIntegral, data)

// 查询即将过期积分
export const findDueSoonIntegral = data => get(URL.findDueSoonIntegral, data)

// 查询积分明细
export const findIntegralDetail = data => post(URL.findIntegralDetail, data)
